@use '../util' as *;

html {
    font-size: 100%;
    box-sizing: border-box;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  body {
    margin: 0;
    padding: 0;
    font-family: var(--font-inter);
  }

  .container {
    max-width: rem(1190);
    margin:0 auto;
  }